:root {
  --text: #131313;
  --main-green: #84a178;
  --card-green: #aec6a5;
  --ingredients-green: #9cbe8f;
  --button-green: #0a8817;
  --grey-dissabled: #ababab;
  --yellow: #f8d24e;
  --card-yellow: #efd478;
  --brown: #d6936d;
  --brown-underlay: #d5753fb2; /*70% transparency - product circle*/
  --green-underlay: #9cbe8fb2; /*70% transparency - product circle*/
  --yellow-underlay: #f8d24eb2; /*70% transparency - product circle*/
  --text-white: #fcfcfc;
  --stroke: #e1e1e1;
  --white: #fff;
  --black: #000;
  --box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.1);
}

body {
  font-family: 'Montserrat', sans-serif;
  color: var(--text);
  font-size: 14px;
  font-weight: 400;
  background-color: var(--main-green);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
  margin: 0;
}

.btn {
  color: var(--text);
  cursor: pointer;
  border-radius: 10px; /*Nie każdy button jest taki! W jednej sekcji jest z border-radius: 8px , dlatego wcześniej tego nie dodałam (MS)*/
  outline: none;
  font-size: 14px;
  font-weight: 600;
  /* display: block; niepotrzebne */ 
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding-left: 0px;
}

a {
  text-decoration: none;
}

svg {
  display: block;
  fill: currentColor;
}

.container {
  max-width: 480px;
  padding: 0 20px;
  margin: 0 auto;
}

.section {
  margin-top: 60px;
  margin-bottom: 60px;
}
.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 16px;
  }
}
